/* Fondo de degradado suave estilo Apple */
.app-container {
  background: linear-gradient(to bottom right, #1c1c1c, #333333);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

h1, h2 {
  font-weight: bold;
}

ul {
  padding: 0;
  list-style: none;
}

.nav-item {
  font-size: 18px;
  text-decoration: none;
  color: white;
  transition: color 0.3s ease;
}

.nav-item:hover {
  color: #00bcd4; /* Efecto de color al pasar el cursor */
}

/* Estilos específicos para los títulos */
.home-title {
  font-family: 'Helvetica Neue', sans-serif;
  font-size: 3rem;
  font-weight: 600;
}

.about-title {
  font-family: 'Georgia', serif;
  font-size: 3rem;
  font-weight: bold;
}

/* Transiciones de página */
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}
